import React,{Fragment, useState, useEffect} from 'react';
import Contenedor from './components/pages/Contenedor'
import Pagination from './components/Pagination'

import PrimarySearchAppBar from './components/layout/partials/PrimarySearchAppBar'
import Footer from './components/layout/partials/Footer'
import axios from 'axios'


import CssBaseline from '@material-ui/core/CssBaseline';
import {  makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
	icon: {
		marginRight: theme.spacing(2),
	},
	heroContent: {
		backgroundColor: theme.palette.background.paper,
		padding: theme.spacing(8, 0, 6),
	},
	heroButtons: {
		marginTop: theme.spacing(4),
	},
	cardGrid: {
		paddingTop: theme.spacing(8),
		paddingBottom: theme.spacing(8),
	},
	card: {
		height: '100%',
		display: 'flex',
		flexDirection: 'column',
	},
	cardMedia: {
		paddingTop: '86.25%', // 16:9
	},
	cardContent: {
		flexGrow: 1,
	},
	footer: {
		backgroundColor: theme.palette.background.paper,
		padding: theme.spacing(6),
	},
}));

function App() {
	const classes = useStyles();

	const [pokemon, setPokemon] = useState([]);
	const [currentPageUrl, setCurrentPageUrl] = useState("https://pokeapi.co/api/v2/pokemon");
	const [nextPageUrl, setNextPageUrl] = useState();
	const [prevPageUrl, setPrevPageUrl] = useState();
	const [loading, setLoading] = useState(true);

	// --------------------
	useEffect(()=>{
		setLoading(true)
		let cancel
		axios.get(currentPageUrl,{
			cancelToken: new axios.CancelToken(c => cancel = c)			
		}).then(res =>{
			setLoading(false)
			console.log(res.data.results);
			setNextPageUrl(res.data.next)
			setPrevPageUrl(res.data.previous)
			setPokemon(res.data.results)
		}).catch((error) => {
            console.log(error)
        })

	 return () => cancel()
  }, [currentPageUrl])


	function gotoNextPage() {
		setCurrentPageUrl(nextPageUrl)
	  }

	 function gotoPrevPage() {
		setCurrentPageUrl(prevPageUrl)
	 }

 
	// --------------------
	return (
		<Fragment>
			<CssBaseline />
			<PrimarySearchAppBar/>
			<main>
				<Contenedor pokemon={pokemon} classes={classes}/>

				<Pagination 
						gotoNextPage={nextPageUrl ? gotoNextPage : null}
		        		gotoPrevPage={prevPageUrl ? gotoPrevPage : null}
					/>
			</main>
			<Footer footer={classes.footer} />
		</Fragment>
	)
}

export default App;
